import React, { Component } from 'react'
import { Route, Switch } from 'react-router-dom'
import Post from './Post'

class Main extends Component {
  render() {
    return (
      <div className="Main">
        <Switch>
          <Route path="/Posts">
            <Post />
          </Route>
        </Switch>
      </div>
    )
  }
}

export default Main
