import React, { Component } from 'react'
import KeyboardEventHandler from 'react-keyboard-event-handler'
import Inko from 'inko'

let inko = new Inko()

// config input keys
// en->kr input keys
let enKey = 'rRseEfaqQtTdwWczxvgASDFGZXCVkoiOjpuPhynbmlYUIHJKLBNM'
let enkeyArr = string2Arr(enKey)
function string2Arr(str) {
  var arr = []
  for (var i = 0; i < str.length; i++) {
    var char = str.charAt(i)
    if (char === char.toUpperCase()) {
      arr.push(`shift+${char.toLowerCase()}`)
    } else {
      arr.push(char)
    }
  }
  return arr
}
let handlingKeys = []

// functional keys
handlingKeys.push('numeric', 'space', 'backspace', '.', ',')

handlingKeys = handlingKeys.concat(enkeyArr)

class Post extends Component {
  state = {
    isEditing: false,
    isCursorOn: true,
    inputText: '|',
  }

  componentDidMount() {
    window.addEventListener('keydown', function (e) {
      if (e.keyCode === 32 && e.target === document.body) {
        e.preventDefault()
      }
    })

    this.cursor = setInterval(this.toggleCursor.bind(this), 500)
  }

  componentWillUnmount() {
    clearInterval(this.cursor)
  }

  toggleCursor() {
    if (!this.state.isEditing) {
      if (this.state.isCursorOn) {
        this.setCursorOff()
      } else {
        this.setCursorOn()
      }
    }
  }

  setCursorOn() {
    this.setState({
      inputText: this.state.inputText + '|',
      isCursorOn: true,
    })
  }

  setCursorOff() {
    if (this.state.inputText.slice(-1) === '|') {
      this.setState({
        inputText: this.state.inputText.slice(0, -1),
        isCursorOn: false,
      })
    }
  }

  setEditing() {
    this.setState({ isEditing: true })
  }

  unSetEditing() {
    this.setState({ isEditing: false })
  }

  applyStroke2State(key) {
    this.setEditing()
    this.setCursorOff()
    switch (key) {
      // shift+key
      case String(key.match(/^shift\+./)):
        this.setState({
          inputText:
            this.state.inputText +
            `${key.charAt(key.length - 1).toUpperCase()}`,
        })
        break
      case 'space':
        this.setState({ inputText: this.state.inputText + ' ' })
        break
      case 'backspace':
        this.setState({ inputText: this.state.inputText.slice(0, -1) })
        break
      default:
        this.setState({ inputText: this.state.inputText + key })
    }
    this.unSetEditing()
    this.setCursorOn()
  }

  render() {
    return (
      <div className="App" style={appStyle}>
        <h2>ÌÇ§Î≥¥Îìú Ïù¥Î≤§Ìä∏ Í∏∞Î∞ò ÌïúÍ∏Ä ÌÉÄÏù¥Ìïë</h2>
        <p
          style={{
            fontSize: '0.5rem',
            textAlign: 'center',
            color: 'MediumAquaMarine',
          }}
        >
          {`ÏúàÎèÑÏö∞ ÏûÖÎ†• en/jp -> ÌïúÍ∏ÄÎ°ú ÏûÖÎ†•`}
          <br />
          ÏûÖÎ†• ÏïàÎê†Ïãú ÏïÑÎ¨¥Í≥≥Ïù¥ÎÇò ÌÅ¥Î¶≠ ÌõÑ ÏûÖÎ†•
        </p>
        <KeyboardEventHandler
          handleKeys={handlingKeys}
          onKeyEvent={(key, e) => this.applyStroke2State(key)}
        />
        <input
          type="textarea"
          disabled={true}
          autoFocus={true}
          id="output-line"
          style={inputStyle}
          value={inko.en2ko(this.state.inputText)}
        />
        <p style={{ color: 'blue', fontSize: '0.5rem', textAlign: 'center' }}>
          logs
          <br />
          2020-07-23: Í∏∞Î≥∏ ÏûÖÎ†• Í∏∞Îä• Ï∂îÍ∞Ä
          <br />
          2020-07-24: ÏåçÏûêÏùå ÏûÖÎ†• Í∏∞Îä• Ï∂îÍ∞Ä
          <br />
        </p>
        <p style={{ color: 'red', fontSize: '0.5rem', textAlign: 'center' }}>
          TODO: ÌäπÏàòÎ¨∏Ïûê ÏûÖÎ†•
        </p>
      </div>
    )
  }
}

const appStyle = {
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'flex-start',
  alignItems: 'center',
  fontSize: '0.3rem',
}

const inputStyle = {
  width: '80%',
  marginTop: '100px',
}

export default Post
